<template>
  <section class="center-view">
    <div class="login">
      <Toast :ToastMessage="ToastMessage" />
      <div class="logo"></div>
      <h2> Alphitter</h2>
      <account-login-form
        @after-form-submit="afterFormSubmit"
        :isProcessing="isProcessing"
        :clearPasswordInput="clearPasswordInput"
      />
      <div class="login__actions">
        <router-link class="link" to="/register"> Alphitter</router-link>
        <span></span>
        <router-link class="link" to="/admin/login"></router-link>
      </div>
    </div>
  </section>
</template>

<script>
import AccountLoginForm from "../components/AccountLoginForm.vue";
import Toast from "../components/AlertToast.vue";
import authorizationAPI from "../api/authorization";

export default {
  name: "login-page",
  components: {
    AccountLoginForm,
    Toast,
  },
  data() {
    return {
      ToastMessage: {
        message: "",
        dataStatus: "",
      },
      // 
      isProcessing: false,
      clearPasswordInput: false,
    };
  },
  methods: {
    async afterFormSubmit(accountDetail) {
      try {
        this.isProcessing = true;
        this.clearPasswordInput = false

        const response = await authorizationAPI.signIn({
          account: accountDetail.account,
          password: accountDetail.password,
        });

        const { data } = response;

        //  &  role
        if (data.status !== "success") {
          this.sendToastMessage(data.message);
          throw new Error(data.message);
        } else if (data.user.role !== "user") {
          this.sendToastMessage("!");
          throw new Error();
        }

        //  token  localStorage 
        localStorage.setItem("token", data.token);

        //  Vuex 
        this.$store.commit("setCurrentUser", data.user);

        this.$router.push({ name: "tweets" });
      } catch (error) {
        console.log(error);

        this.isProcessing = false;
        this.clearPasswordInput = true
        return;
      }
    },
    //  toast message  toast 
    sendToastMessage(message) {
      this.ToastMessage.dataStatus = "";
      this.ToastMessage.dataStatus = "error";
      this.ToastMessage.message = message;
    },
  },
};
</script>
